import React, { memo } from 'react';
import PropTypes from 'prop-types';
import _size from 'lodash/size';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import { withStyles } from '@material-ui/core/styles';
import Loading from '../../../components/Loading';
import DeliveriesItemRow from '../DeliveriesItemRow';
import styles from './styles';

const DeliveriesTable = memo(({ classes, deliveries, loading }) => (
  <div className={classes.container}>
    <Paper className={classes.paper}>
      <Table>
        <TableHead>
          <TableRow>
            <TableCell className={classes.tableCell}>DATA</TableCell>
            <TableCell className={classes.tableCell}>ENTREGA</TableCell>
            <TableCell className={classes.tableCell}>TIPO</TableCell>
            <TableCell className={classes.tableCell}>OCUPANTE</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {
            loading ?
              <TableRow>
                <TableCell colSpan={4} align="center">
                  <Loading />
                </TableCell>
              </TableRow>
              :
              (
                _size(deliveries) ?
                  (deliveries.map((delivery, i) => {
                    return (
                      <DeliveriesItemRow
                        index={i}
                        key={delivery.id}
                        delivery={delivery}
                        loading={loading}
                      />
                    );
                  }))
                  :
                  <TableRow>
                    <TableCell className={classes.empty} colSpan={4} align="center">
                      Nenhuma entrega registrada
                    </TableCell>
                  </TableRow>
              )
          }
        </TableBody>
      </Table>
    </Paper>
  </div>
));

DeliveriesTable.propTypes = {
  deliveries: PropTypes.array,
  loading: PropTypes.bool,
  classes: PropTypes.object
};

export default withStyles(styles, { withTheme: true })(DeliveriesTable);
