import React, { memo } from 'react';
import PropTypes from 'prop-types';
import { Select, MenuItem } from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';
import LoadingCircle from '../../../../components/LoadingCircle';
import RolesEnum from '../../../../enums/roles';
import styles from './styles';

const { COMPANY_ROLES } = RolesEnum;

const RoleSelect = memo(({ classes, updateBuildingRoleLoading, role, onChangeRole }) => (
  updateBuildingRoleLoading ?
    <div>
      <LoadingCircle />
      <span>Atualizando...</span>
    </div>
    :
    <div>
      <Select
        className={classes.roleSelect}
        value={role}
        onChange={onChangeRole}
      >
        {COMPANY_ROLES.map((buildingRole) => (
          <MenuItem key={buildingRole.value} value={buildingRole.value}>
            {buildingRole.label}
          </MenuItem>
        ))}
      </Select>
    </div>
));

RoleSelect.propTypes = {
  role: PropTypes.string,
  classes: PropTypes.object,
  onChangeRole: PropTypes.func,
  updateBuildingRoleLoading: PropTypes.bool
};

export default withStyles(styles, { withTheme: true })(RoleSelect);
