import React, { memo } from 'react';
import { compose } from 'recompose';
import PropTypes from 'prop-types';
import { get as _get, find as _find } from 'lodash';
import { TableCell, TableRow } from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';
import withRequest from '../../../utils/with-request';
import withViewerData from '../../../utils/with-viewer-data';
import MembersService from '../../../services/members';
import BlockMember from './BlockMember';
import RoleSelect from './RoleSelect';
import styles from './styles';
import RolesEnum from '../../../enums/roles';

const { COMPANY_ROLES, COMPANY_MANAGER_ROLE, MEMBER_ROLE } = RolesEnum;

const MembersItemRow = memo(({
  viewer, index, member, loggedMemberId, classes,
  updateBuildingRoleLoading, updateBuildingRoleFetch,
  memberBlockedAtLoading, memberBlockedAtFetch
}) => {
  if (!viewer) return null;
  const onChangeRole = (e, { props: { value } }) => {
    const options = {
      variables: {
        memberId: member.id,
        buildingId: viewer.building_id,
        role: value
      }
    };
    updateBuildingRoleFetch(true, '', null, options).catch(() => {});
  };
  const buildingEnterpriseUsers = _get(member, 'enterprise_user.buildings_enterprise_users');
  const buildingEnterpriseUser = _find(
    buildingEnterpriseUsers, b => b.building_id === viewer.building_id
  );
  const userRole = _get(buildingEnterpriseUser, 'role', MEMBER_ROLE);
  const viewerBuildingEnterpriseUsers = _get(viewer, 'buildings_enterprise_users');
  const viewerBuildingEnterpriseUser = _find(
    viewerBuildingEnterpriseUsers, b => b.building_id === viewer.building_id
  );
  const viewerRole = _get(viewerBuildingEnterpriseUser, 'role');
  const memberFullName = `${_get(member, 'name')} ${_get(member, 'lastname')}`;
  const memberEmail = _get(member, 'email');
  const memberPhone = _get(member, 'phone');
  const blockedAt = Boolean(_get(member, 'building_member.blocked_at'));
  return (
    <TableRow className={index % 2 ? classes.bgWhite : classes.bgGray}>
      <TableCell className={classes.tableSmallCell}>
        {memberFullName}
      </TableCell>
      <TableCell className={classes.tableSmallCell}>
        {
          viewerRole === COMPANY_MANAGER_ROLE && member.id !== viewer.member.id ?
            <RoleSelect
              updateBuildingRoleLoading={updateBuildingRoleLoading}
              role={userRole}
              onChangeRole={onChangeRole}
            />
            :
            _get(_find(COMPANY_ROLES, r => r.value === userRole), 'label')
        }
      </TableCell>
      <TableCell className={classes.tableSmallCell}>
        {memberEmail}
      </TableCell>
      <TableCell className={classes.tableSmallCell}>
        {memberPhone}
      </TableCell>
      <TableCell className={classes.tableCell}>
        {
          loggedMemberId !== member.id &&
            <BlockMember
              loading={memberBlockedAtLoading}
              blockedAt={!!blockedAt}
              onConfirmBlock={() => memberBlockedAtFetch(true)}
            />
        }
      </TableCell>
    </TableRow>
  );
});

MembersItemRow.propTypes = {
  index: PropTypes.number,
  member: PropTypes.object,
  viewer: PropTypes.object,
  classes: PropTypes.object,
  updateBuildingRoleFetch: PropTypes.func,
  updateBuildingRoleLoading: PropTypes.bool,
  memberBlockedAtLoading: PropTypes.bool,
  memberBlockedAtFetch: PropTypes.func,
  loggedMemberId: PropTypes.string
};

const withUpdateBuildingRole = withRequest(MembersService.updateBuildingRole, {
  name: 'updateBuildingRole',
  init: false,
  options: ({ refetch, setAlert }) => {
    return ({
      onRequestFinished: refetch,
      onRequestError: () => {
        setAlert({
          alertMsg: 'Ocorreu um erro na atualizao desse papel',
          alertType: 'error',
          showAlert: true
        });
      }
    });
  }
});

const withBlockedAt = withRequest(MembersService.setBlocked, {
  name: 'memberBlockedAt',
  init: false,
  options: ({
    refetch,
    member: {
      id: memberId,
      building_member: { blocked_at }
    }
  }) => {
    return ({
      variables: {
        memberId,
        updatingAttributes: {
          blocked_at: !blocked_at
        }
      },
      onRequestFinished: refetch
    });
  }
});


export default compose(
  withStyles(styles, { withTheme: true }),
  withUpdateBuildingRole,
  withViewerData,
  withBlockedAt
)(MembersItemRow);
